/*
 FretPnl.css
  - by Chris DeFreitas, ChrisDeFreitas777@gmail.com
  - styles to manage construction of Fretboard frets and strings etc
*/

.fretPnl{ border:0; border-collapse:separate; border-spacing:0px; table-layout:fixed;
  padding:0 0 0 10px; margin:0 auto;
  position:relative; width:auto;
}
.fretPnl tr{ 
  border:0;
}
.fretPnl tr td{   /* default cells */
  background-color:#3d3b4fff; border:0; padding:0; 
  height:2em; max-height:2em; width:5em; min-width:5em; position:relative;
}
.fretPnl .strg .fret{ cursor:pointer; position:relative; }   /* all fret cells */
 
.fretPnl .frameTop td[data-fretfilter="true"]{      /* filtered fret numbers */
  color:#3c31a7; 
}
.fretPnl .frameTop td .fretFilterDiv {     /* show a bar every third fret when qry.collpased=true */
  border:none; height:20px; width:91%; margin:0 0 0 3px;
  text-shadow:1px 1px 5px #0008;
}
.fretPnl .frameTop td .fretFilterCollapsed {     /* show a bar every third fret when qry.collpased=true */
  border-bottom:solid #71b578 3px; height:17px; width:91%; margin:0 0 0 3px;
}
.fretPnl tr .fret[data-fretfilter="true"] .fretbar{      /* filtered fretbar */
  background:#3a3a3a; 
}
.fretPnl tr .fret[data-fretfilter="true"]{      /* filtered frets */
  cursor:default;
}
.fretbar{
  width:1px; height:100%;
  background-color:#999;
  border:0;
  border-right:1px solid #335;
  float:right;
}
.fretPnl tr .fret0{     /* fret 0 */
  background-color:transparent; border:0; width:7em; min-width:7em;
}
.fretPnl tr .fret0 .fretbar{      /* bar */
  width:7px; 
  background-color:#6f6f7f;
}
.fretPnl .frame .fret1{  /* hide bar in frames */
  background-color:transparent; border:0;
}
.fretPnl .strg1 .tdBorder0{  /*  */
  border:0;
}
.fretPnl tr .borderRight,
.fretPnl .frame .borderBottom,
.fretPnl .frame .borderRight{      /* format right border  */
  background-color:transparent; width:0.5em;  min-width:0.5em;
}
.fretPnl .borderBottom .borderRight{
  border-bottom:0;
}
.fretPnl tr td:last-child{ /* end of board */
  border-right:0; width:1.5em;
}

.fretPnl .strg1 td{ height:1em;  }    /*  height of top/bottom edge  */
.fretPnl .borderBottom td{ cursor:pointer; height:1.25em; }

.fretPnl .frame td{    /* format td's in frame and border rows */
  background-color:transparent; cursor:pointer; 
  color:#ffffff80;
  font-size:1em;
  /* text-shadow:1px 1px 5px #0008; */
  text-align:center;
  height:0.5em;
}
.fretPnl .frameBottom .tdBottom{ cursor:default; position:relative; }

/* strings */
.fretPnl tr[data-fltr="true"] .stringdiv{ background-color:#ffffff20; }
.fretPnl .stringdiv{ background-color:#fffffff0;  
  position:absolute; top:100%; left:0px;
  height:1px; width:100%; z-index:99;
}
.fretPnl .fret14 .stringdiv{ width:120%;  }
.fretPnl .strg3 .stringdiv,
.fretPnl .strg4 .stringdiv,
.fretPnl .strg5 .stringdiv{ height:2px;  }
.fretPnl .strg6 .stringdiv{ height:2.5px;  }

/* string filter buttons */
.fretPnl .btnStrgFltr{ background:#177d7f; border:none; border-radius:5px;
  color:#177d7f; cursor:pointer; outline:0; z-index:100;
  position:absolute; font-size:16px; width:32px; left:px; 
}
.fretPnl .btnStrgFltr1{  top:7px; }
.fretPnl .btnStrgFltr2{  top:23px; }
.fretPnl .btnStrgFltr3,
.fretPnl .btnStrgFltr4,
.fretPnl .btnStrgFltr5,
.fretPnl .btnStrgFltr6{  top:24px; }

/*
  FretButton.css
  - by Chris DeFreitas, ChrisDeFreitas777@gmail.com
  - styles for buttons displayed by FretPnl.js
  
*/

.fretButton{ 
  color:#000; border:none; border-radius:5px;  box-shadow:1px 1px 5px #0007;
  text-align:left; margin:0; padding-left:10px;
  position:absolute; top:5px; left:15px; height:2em; width:4.5em;
  z-index:100;
}
.fretButton{ background:linear-gradient(180deg, #fff,#eec 50%,hsl(60deg 5% 36%) 100%); }

/* position over string */
.fret0 .fretButton { left:39px; }
.strg2 .fretButton,
.strg3 .fretButton,
.strg4 .fretButton{ top:calc( 1em + 7px) }
.strg5 .fretButton,
.strg6 .fretButton{ top:calc( 1em + 8px) }

/* appearance of interval captions */
.fretButton[data-ivl="P1"] sup{ color: #fff; text-shadow:1px 1px 1px #441616; }
.fretButton[data-ivl="m3"] .supInterval,
.fretButton[data-ivl="M3"] .supInterval{ color: #fff; text-shadow:1px 1px 1px #000; }
.fretButton[data-ivl="P5"] .supInterval{ color: #fff; text-shadow:1px 1px 1px #000; }
.fretButton[data-ivl="m7"] .supInterval,
.fretButton[data-ivl="M7"] .supInterval{ color: #fff; text-shadow:1px 1px 1px #000;  }

.fretButton[data-state="fretRoot"]{ border: 1px #04ff00 solid; } /* green */
.fretButton[data-state="noteSelect"]{ border: 1px #fffb00 solid;  } /* yellow */
.fretButton[data-state="fretSelect"]{ border: 1px #00afff solid;  }  /* azure radiance */
.fretButton[data-state="fretSelectMatch"]{ background:#b1e6ff; }  /* Anakiwa */
.fretButton[data-state="fretSelectMatch1"]{ background:#41c1ed; }  /* root note */
.fretButton[data-state="noteFilter"]{ background:#aaf; }  /* Melrose */
.fretButton[data-state="interval"]{ background:#ffff9b; }  /*  pale yellow */
.fretButton[data-state="chord"]{ background:#ffc06f; }  /*  pale orange */
.fretButton[data-state="chord1"]{ background:#f90; }  /*  pale orange */
.fretButton[data-state="chord0"]{ background:#f90; border: 1px #04ff00 solid; }  /*  pale orange, fret root */
.fretButton[data-state="chordShape"]{ background:#f1601e; }
.fretButton[data-state="chordShape0"]{ background:#b04a1b; border: 1px #04ff00 solid; }
.fretButton[data-state="chordShape1"]{ background:#b04a1b;  }
.fretButton[data-state="invr"]{ background:#e7e; }
.fretButton[data-state="invr1"]{ background:#e2e; }
.fretButton[data-state="triad"]{ background:#00e0e0; }
.fretButton[data-state="triad1"]{ background:#00a8a8; }

/* .fretButton[data-selected="0"] {} */
.fretButton[data-selected="1"] { background:#3ac664; }  /* emerald */

/* format button text/caption */
.fretButton sup{ color:#0f000fa0; font-size:0.7em; position:absolute; top:0px; text-shadow:none;  }
.fretButton sub{ color:#00000050; text-shadow:none; position:relative; }
.fretButton .subInterval{ cursor:alias; top:0px; right:-12px; }
.fretButton .abc{ cursor:alias; top:-15px; right:-25px; display:block; max-width:20px; }
.fretButton .ivlabc{ cursor:alias; top:-12px; right:-25px; display:block; max-width:20px; }
.fretButton .tab{ cursor:alias; top:-1px; right:-12px; }
.fretButton .spanNote .subOctave{ color:#0008; font-size:0.6em; }
.fretButton .spanIvl span{ color:#0007; font-size:0.7em; }
.fretButton .subNote{ cursor:alias; top:0px; right:-12px; }

/* all safari browsers
@media screen and (min-color-index:0) and(-webkit-min-device-pixel-ratio:0)
{ @media {
*/

/* Safari 11+ */
@media not all and (min-resolution:.001dpcm)
{ @supports (-webkit-appearance:none) and (stroke-color:transparent) {
  
  /* adjust button captions */
  .fretButton .spanNote { display:block; position:relative; left:-5px; top:0px; }
  .btnFlatNote{ letter-spacing:-4px; position:relative; top:-2px; }
  .fretButton .subInterval{ cursor:alias; top:-15px; right:-20px; }
  .fretButton .abc{ cursor:alias; top:-16px; right:-15px; display:block; max-width:15px; }
  .fretButton .ivlabc{ cursor:alias; top:-14px; right:-15px; display:block; max-width:15px; }
  .fretButton .ivltab{ cursor:alias; top:0px; right:-5px; }
  .fretButton .notetab{ cursor:alias; top:-15px; right:-20px; }

  /* position fretButton over string */
  .strg1 .fretButton{ top:calc( 6px) }
  .strg2 .fretButton{ top:calc( 1em + 11px) }
  .strg3 .fretButton,
  .strg4 .fretButton{ top:calc( 1em + 12.5px) }
  .strg5 .fretButton,
  .strg6 .fretButton{ top:calc( 1em + 12px) }
}}

/*
  Fretboard.css
  - by Chris DeFreitas, ChrisDeFreitas777@gmail.com
  - styles for Fretboard and QueryPnl
  
*/

.fretboard{ display:inline-block;  margin:0 auto 0 auto; position:relative; }
.fretboard[id="Fretboard0"]{ margin:1em auto 0 auto;}

.queryPnl{ color:#ffffffd0; border:none; outline:none; padding:0;
  position:relative; margin:0 auto; text-align:center; top:-0.25em;
}

.tbQuery{ 
  border:0; border-collapse:separate; border-spacing:0px; table-layout:fixed;
  padding:0; margin:0 auto; position:relative; width:auto; max-width:70%;
}
.tbQuery tr td{ vertical-align:top; }
.tdQryContent{ padding:0; width:70%; } 


.queryControls{ 
  background-color:hsl(177deg 68% 31% / 92%); 
  border:1px solid #333;  border-radius:5px; box-shadow:5px 5px 15px 1px #0000007a;
  display:block; height:3.5em; margin:0; overflow:hidden; padding:0; width:100%; 
  position:relative; 
}
/* .queryPnlHide .queryControls, */
.queryControls[data-roottype='fretSelect']{ display:none; }

.dataPnl { border:none; display:inline-block; padding:0 0.5em 0.5em 0.5em; text-align:center;  }
.dataPnl label{ color:#ffffffd0; font-weight:bold; font-size:1em; text-shadow:1px 1px 5px #00000090; }
.dataPnl label[data-selected="true"]{ cursor:pointer; }
.dataPnl select{ background-color:#ffffffa0; border:none; border-radius:3px; cursor:pointer;
  display:block; margin:0 auto; padding:5px 0;
  min-width:5em;  max-width:10em; 
  outline:0;  text-align-last:center;
}
/*.dataPnl select option{}   not applicable because controlled by OS */
.dataPnl select[data-state="interval"]{ background-color:#ffff9b; }
.dataPnl select[data-state="chord"]{ background-color:#eab555; }

.tdQryBtnsLeft{ padding:0.5em 0.25em 0 0; text-align:right; width:8em; } 
.tdQryBtnsRight{ padding:0.5em 0 0 0.5em; text-align:left; width:16em; } 
/* .queryPnlHide .tdQryBtnsLeft, 
.queryPnlHide .tdQryBtnsRight{ padding-top:0; }
*/
.qryBtn{ background-color:hsl(177deg 68% 31% / 92%); border-radius:5px; 
  box-shadow: 5px 5px 15px 1px #0000007a; color:#000; cursor:pointer; 
  display:inline-block; margin:0 0.25em;
  font-weight:normal; text-shadow:1px 1px 1px #fff6; text-align:center;
  font-size:1.25em; padding:2px 5px; width:1em;
 }
/* .qryBtnHelp{  } */
.qryBtnSmall{
  background-color:hsl(177deg 68% 31% / 92%); border-radius:5px; 
  box-shadow: 5px 5px 15px 1px #0000007a; color:#000; cursor:pointer; 
  display:inline-block; margin:0 0.25em; 
  font-weight:normal; text-shadow:1px 1px 1px #fff6; text-align:center;
  font-size:1em; padding:3px 6px;
}


.infoPnl{ background:#00000045; border-radius:5px; margin:0 auto; width:98%; } 
.queryPnlShow .infoPnl{ display:block; margin:0 auto; }
/* .queryPnlHide .infoPnl{ display:inline-block; } */
.infoDiv{ display:inline-block;
   padding:0; margin:0 auto; text-shadow:1px 1px 3px #000; width:100%; 
 }
.infoDiv .propName{ color:white; cursor:pointer; font-weight:bold; margin:0 0 0 0.5em; }
.infoDiv .lineBreak{ line-height:5%; }
.infoDiv .propName:first-child{ margin:0; }
.infoDiv .propName[data-selected="note"]{ display:block; font-size:0.8em; font-weight:normal; font-style:italic; }
.infoDiv .ivl{ border-radius:3px; cursor:pointer; font-weight:normal; }
.infoDiv .ivl:hover{ background:#7f77;  }
.infoDiv .ivl[data-selected="noteFilter"] { background:#aaf; border-radius:3px; }  /* note in props.noteFilter */
.infoDiv .ivl[data-selected="fretSelectMatch"] { background:#b1e6ff7a; border-radius:3px; }  /* user selected matching chord or scale */
.infoDiv .ivl[data-selected="invr"]{ background-color:#e7e; }  
.infoDiv .ivl[data-selected="triad"]{ background-color:#0cc; }  
.infoDiv .ivl[data-selected="chordShape"]{ background-color:#f1601e; }  
.infoDiv .ivl sub{ font-size:0.6em; position:relative; left:-3px; top:-3px; }



.ArrowBtn{
  background-color:hsl(177deg 68% 31% / 92%); 
  border-radius:5px; box-shadow: 5px 5px 15px 1px #007; 
  color:#000; cursor:pointer; 
  display:inline-block; margin:0 0.25em; outline:0; padding:0; 
  text-shadow:1px 1px 1px #fff6; text-align:center;
}
.ArrowBtn div{ border:none; padding:0; position:relative; outline:0; }
.ArrowBtnUp div{ text-shadow:-1px 1px 1px #fff6; }
.ArrowBtnDn div{  text-shadow:1px -1px 1px #fff6; }


