/*
  Dialog.css
  - custom style for reactjs-popup
  - for GuitarJoe by Chris DeFreitas

*/

.popup-content{background:#282c34; border:3px solid #7771; border-radius:15px;
  color:#fff; 
  margin:auto; padding:0 1em; max-width:40em;
}
[role=tooltip].popup-content{ box-shadow:0 0 3px rgba(0,0,0,.16);border-radius:5px }
.popup-overlay{background:rgba(0,0,0,.5); }
[data-popup=tooltip].popup-overlay{ background:transparent; }
.popup-arrow{
  -webkit-filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16)); filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));
  stroke-width:1px;stroke:#d7d7d7;stroke-dasharray:30px; stroke-dashoffset:-54px;
  color:#fff; left:0;right:0;top:0;bottom:0
 }

